import {
  CREATE_RELEASE,
  GET_ALL_RELEASES_LOADING,
  GET_ALL_RELEASES,
  CREATE_RELEASE_LOADING,
  CREATE_RELEASE_ERROR,
  GET_RELEASE,
  CLEAR_RELEASE_POPUP,
  DELETE_RELEASE,
  EDIT_RELEASE,
  RELEASE_ERROR,
  DELETE_RELEASE_FAIL,
  DELETE_RELEASE_LOADING,
  DELETE_RELEASE_SUCCESS,
} from "./../types";

const initialState = {
  release_error: true,
  release_success: null,
  release: {},
  msg: "",
  releases: [],
  contents: [],
  releases_loading: false,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case GET_ALL_RELEASES:
      return {
        ...state,
        releases: action.payload.reverse(),
        releases_loading: false,
        msg: action.payload.msg,
      };

    case DELETE_RELEASE:
      return {
        ...state,
        releases: action.payload,
        releases_loading: false,
        release_success: true,
        msg: action.msg,
      };

    case GET_ALL_RELEASES_LOADING:
      return {
        ...state,
        releases_loading: true,
        release_success: false,
        release_error: false,
      };
    case CLEAR_RELEASE_POPUP:
      return {
        ...state,
        release_error: false,
        release_success: false,
        releases_loading: false,
        msg: "",
      };
    case CREATE_RELEASE_LOADING:
      return {
        ...state,
        release_loading: true,
        release_error: null,
        release_success: false,
      };
    case CREATE_RELEASE_ERROR:
      return {
        ...state,
        release_loading: false,
        release_error: true,
        release_success: false,
        msg: action.payload,
      };
    case CREATE_RELEASE:
      return {
        ...state,
        release_loading: false,
        release_success: true,
        release_error: null,
        release: action.payload,
        msg: action.msg,
      };
    case GET_RELEASE:
      return {
        ...state,
        release_loading: false,
        release_success: false,
        release_error: null,
        release: action.payload,
      };
    default:
      // return state;
      return { ...state };
  }
};
