export const deleteTasksAction = (id) => async (dispatch) => {
  dispatch({
    type: GET_ALL_TASKS_LOADING,
    payload: true,
  });
  try {
    let tasks = await deleteTaskApi(id);
    let delete_msg = await (await deleteTaskApi(id)).data.msg;
    tasks = await getAllTaskApi();
    console.log({ tasks: tasks.data.data });
    console.log({ delete_msg });
    dispatch({
      type: DELETE_TASK,
      payload: tasks.data.data,
      msg: delete_msg,
    });
  } catch (error) {
    console.log({ err: error.response });
    console.log({ err: error.message });
    if (error.response) {
      error = error.response.msg;
    } else {
      error = "Something went wrong. Try Again";
    }
    dispatch({
      type: CREATE_TASK_ERROR,
      payload: [],
    });
  }
};
