import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import {
  RegisterAction,
  clearAuthErrorAction,
} from "./../../store/actions/Auth";
import Loader from "react-loader-spinner";
import { FailModal } from "./../../components/Snacks/Response";

class Register extends Component {
  constructor(props) {
    super(props);
    this.state = {
      full_name: "",
      email: "",
      phone: "",
      userType: "Admin",
      password: "",
      error: "",
    };

    this.onChange = this.onChange.bind(this);
    this.register = this.register.bind(this);
    this.onClearError = this.onClearError.bind(this);
  }
  onChange(e) {
    e.preventDefault();
    this.setState({
      [e.target.name]: e.target.value,
      error: "",
    });
  }

  onClearError() {
    this.props.clearAuthErrorAction();
    this.setState({ error: "" });
  }

  register() {
    let { full_name, email, phone, userType, password } = this.state;
    if (email && password) {
      this.props.RegisterAction(
        { email, password, userType, phone, full_name },
        this.props.history
      );
    }
  }

  render() {
    let { error_msg, auth_loading } = this.props.auth;
    return (
      <>
        {(error_msg || this.state.error) && (
          <FailModal
            msg={error_msg || this.state.error}
            onStop={this.onClearError}
          />
        )}
        <div className="container mx-auto px-4 mt-5 h-full">
          <div className="flex content-center items-center justify-center h-full">
            <div className="w-full lg:w-6/12 px-4">
              <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-blueGray-200 border-0">
                <div className="rounded-t mb-0 px-6 py-6">
                  <div className="text-center mb-3">
                    <h6 className="text-blueGray-500 text-sm font-bold">
                      Sign up
                    </h6>
                  </div>
                </div>
                <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
                  <form>
                    <div className="relative w-full mb-3">
                      <label
                        className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                        htmlFor="grid-password"
                      >
                        Full Name
                      </label>
                      <input
                        name="full_name"
                        value={this.state.full_name}
                        disabled={auth_loading && true}
                        onChange={this.onChange}
                        required={true}
                        className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                        placeholder="Full Name"
                      />
                    </div>

                    <div className="relative w-full mb-3">
                      <label
                        className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                        htmlFor="grid-password"
                      >
                        Email
                      </label>
                      <input
                        type="email"
                        name="email"
                        value={this.state.email}
                        disabled={auth_loading && true}
                        onChange={this.onChange}
                        required={true}
                        className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                        placeholder="Email"
                      />
                    </div>
                    <div className="relative w-full mb-3">
                      <label
                        className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                        htmlFor="grid-password"
                      >
                        Phone Number
                      </label>
                      <input
                        type="number"
                        name="phone"
                        value={this.state.phone}
                        disabled={auth_loading && true}
                        onChange={this.onChange}
                        required={true}
                        className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                        placeholder="Phone Number"
                      />
                    </div>

                    <div className="relative w-full mb-3">
                      <label
                        className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                        htmlFor="grid-password"
                      >
                        Password
                      </label>
                      <input
                        type="password"
                        name="password"
                        value={this.state.password}
                        disabled={auth_loading && true}
                        onChange={this.onChange}
                        required={true}
                        className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                        placeholder="Password"
                      />
                    </div>

                    <div>
                      <label className="inline-flex items-center cursor-pointer">
                        <small>Already sign up .?</small>
                        <Link to="/login" className="ml-1 text-muted -200">
                          <small className="text-blue"> Login</small>
                        </Link>
                      </label>
                    </div>

                    <div className="text-center mt-6">
                      {auth_loading ? (
                        <button
                          className="bg-blueGray-800 text-center heart text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                          type="button"
                        >
                          <Loader
                            type="Oval"
                            color="white"
                            height={21}
                            width={21}
                            secondaryColor={"white"}
                          />
                        </button>
                      ) : (
                        <button
                          className="bg-blueGray-800 text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                          type="button"
                          onClick={this.register}
                        >
                          Register
                        </button>
                      )}
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
});
export default connect(mapStateToProps, {
  RegisterAction,
  clearAuthErrorAction,
})(withRouter(Register));
