import React, { Component } from "react";
import { getAllReleasesAction } from "./../../store/actions/Release";
import { getAllTasksAction } from "./../../store/actions/Task";

import HeaderStats from "./../../components/Headers/HeaderStats.js";
import { connect } from "react-redux";
class Dashboard extends Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    this.props.getAllReleasesAction();
    this.props.getAllTasksAction();
  }

  render() {
    let { releases, tasks } = this.props;
    function analysis(tasks, releases) {
      let completeReleasesNo = releases.filter(
        (data) => data.failed !== undefined
      )
        ? releases.filter((data) => data.failed !== undefined).length
        : 0;
      let uncompleteReleasesNo = releases.filter(
        (data) => data.failed === undefined
      )
        ? releases.filter((data) => data.failed === undefined).length
        : 0;

      let successTasksNo = tasks.filter((data) => data.status !== "success")
        ? tasks.filter((data) => data.status === "success").length
        : 0;
      let failedTasksNo = tasks.filter((data) => data.status === "failed")
        ? tasks.filter((data) => data.status === "failed").length
        : 0;
      let pendingTasksNo = tasks.filter((data) => data.status === "pending")
        ? tasks.filter((data) => data.status === "pending").length
        : 0;

      let pendingTasksNoPecent = `${
        (Number(pendingTasksNo) / tasks.length) * 100
      }%`;
      let successTasksNoPecent = `${
        (Number(successTasksNo) / tasks.length) * 100
      }%`;
      let failedTasksNoPecent = `${
        (Number(failedTasksNo) / tasks.length) * 100
      }%`;
      return {
        releasesNo: releases.length,
        completeReleasesNo,
        uncompleteReleasesNo,

        tasksNo: tasks.length,
        successTasksNo,
        failedTasksNo,
        pendingTasksNo,

        pendingTasksNoPecent,
        successTasksNoPecent,
        failedTasksNoPecent,
      };
    }
    return (
      <>
        <div className="px-4 md:px-10 mx-auto mt-4 w-full -m-24">
          <HeaderStats {...analysis(tasks, releases)} />
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  releases: state.release.releases,
  tasks: state.task.tasks,
});

export default connect(mapStateToProps, {
  getAllReleasesAction,
  getAllTasksAction,
})(Dashboard);
