import React, { Component } from "react";
import { Link } from "react-router-dom";
import { LoginAction, clearAuthErrorAction } from "./../../store/actions/Auth";
import { connect } from "react-redux";
import Loader from "react-loader-spinner";
import { withRouter } from "react-router-dom";

import { FailModal } from "./../../components/Snacks/Response";

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      error: "",
    };

    this.onChange = this.onChange.bind(this);
    this.login = this.login.bind(this);
    this.onClearError = this.onClearError.bind(this);
  }
  onChange(e) {
    e.preventDefault();
    this.setState({
      [e.target.name]: e.target.value,
      error: "",
    });
  }

  onClearError() {
    this.props.clearAuthErrorAction();
    this.setState({ error: "" });
  }

  login() {
    let { email, password } = this.state;
    if (email && password) {
      this.props.LoginAction({ email, password }, this.props.history);
    }
  }

  render() {
    let { error_msg, auth_loading } = this.props.auth;
    return (
      <>
        {(error_msg || this.state.error) && (
          <FailModal
            msg={error_msg || this.state.error}
            onStop={this.onClearError}
          />
        )}
        <div className="container mt-5 mx-auto px-4 h-full">
          <div className="flex content-center items-center justify-center h-full">
            <div className="w-full lg:w-4/12 px-4">
              <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-blueGray-200 border-0">
                <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
                  <div className="text-blueGray-400  text-center mb-3 font-bold">
                    <div className="pt-5 mt-3">Log in</div>
                  </div>
                  <form>
                    <div className="relative w-full mb-3">
                      <label
                        className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                        htmlFor="grid-password"
                      >
                        Email
                      </label>
                      <input
                        disabled={auth_loading && true}
                        required
                        type="email"
                        name="email"
                        value={this.state.email}
                        onChange={this.onChange}
                        className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                        placeholder="Email"
                      />
                    </div>

                    <div className="relative w-full mb-3">
                      <label
                        className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                        htmlFor="grid-password"
                      >
                        Password
                      </label>
                      <input
                        disabled={auth_loading && true}
                        required
                        name="password"
                        value={this.state.password}
                        onChange={this.onChange}
                        type="password"
                        className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                        placeholder="Password"
                      />
                    </div>
                    <div>
                      <label className="inline-flex items-center cursor-pointer">
                        <span className="ml-2 text-sm font-semibold text-blueGray-600">
                          <Link to="/register" className="text-muted -200">
                            <small>Create new account</small>
                          </Link>
                        </span>
                      </label>
                    </div>

                    <div className="text-center mt-6">
                      {auth_loading ? (
                        <button
                          className="bg-blueGray-800 text-center heart text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                          type="button"
                        >
                          <Loader
                            type="Oval"
                            color="white"
                            height={21}
                            width={21}
                            secondaryColor={"white"}
                          />
                        </button>
                      ) : (
                        <button
                          className="bg-blueGray-800 text-white active:bg-blueGray-600 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                          type="button"
                          onClick={this.login}
                        >
                          Sign In
                        </button>
                      )}
                    </div>
                  </form>
                  {/* <div className="text-center mt-2 text-sm text-red-500">
                    {this.state.error || error_msg}
                  </div> */}
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps, { LoginAction, clearAuthErrorAction })(
  withRouter(Login)
);
