import {
  CREATE_RELEASE,
  CLEAR_RELEASE_POPUP,
  CREATE_RELEASE_LOADING,
  CREATE_RELEASE_ERROR,
  GET_ALL_RELEASES,
  GET_ALL_RELEASES_LOADING,
  DELETE_RELEASE,
  GET_RELEASE,
} from "./../types";
import {
  createReleaseApi,
  getAllReleaseApi,
  deleteReleaseApi,
  updateReleaseApi,
} from "./../api/service";

export const clearReleaseAction = () => async (dispatch) => {
  console.log("close");
  dispatch({
    type: CLEAR_RELEASE_POPUP,
  });
};

export const pickReleaseAction = (releaseData, history) => async (dispatch) => {
  dispatch({
    type: GET_RELEASE,
    payload: releaseData,
  });
  history.push("/admin/release");
};

export const createReleaseAction = (releaseData) => async (dispatch) => {
  dispatch({
    type: CREATE_RELEASE_LOADING,
    payload: true,
  });
  try {
    let create = await createReleaseApi(releaseData);
    console.log({ register: create.data.data });
    dispatch({
      type: CREATE_RELEASE,
      payload: create.data.data,
      msg: create.data.msg,
    });
    console.log({ msg: create.data.msg });
  } catch (error) {
    console.log({ err: error.response });
    if (error.response) {
      error = error.response.data.msg;
    } else {
      error = "Something went wrong. Try Again";
    }
    dispatch({
      type: CREATE_RELEASE_ERROR,
      payload: error,
    });
  }
};

export const editReleaseAction = (releaseId, releaseData) => async (
  dispatch
) => {
  console.log({ releaseId, releaseData });
  dispatch({
    type: CREATE_RELEASE_LOADING,
    payload: true,
  });
  try {
    console.log({ releaseData, releaseId });
    let create = await updateReleaseApi(releaseId, releaseData);

    console.log({ register: create.data });
    dispatch({
      type: CREATE_RELEASE,
      payload: create.data.data,
      msg: create.data.msg,
    });
  } catch (error) {
    console.log({ err: error.response });
    if (error.response) {
      error = error.response.data.msg;
    } else {
      error = "Something went wrong. Try Again";
    }
    dispatch({
      type: CREATE_RELEASE_ERROR,
      payload: error,
    });
  }
};

export const getAllReleasesAction = (releaseData) => async (
  dispatch,
  state
) => {
  dispatch({
    type: GET_ALL_RELEASES_LOADING,
    payload: true,
  });
  try {
    let releases = await getAllReleaseApi();
    console.log({ releases: releases.data.data });

    let auth_type = state().auth.auth_type;
    let auth_id = state().auth.profile._id;
    releases = await releases.data.data;

    if (auth_type === "Instructor") {
      let new_releases = [];
      let chc = releases.filter((data) => data.instructors.length > 0);
      console.log({ ins: chc });
      for (let chck of chc) {
        console.log({ ins: chck.instructors });
        for (let chck2 of chck.instructors) {
          console.log({ id: chck2._id, auth_id });
          if (chck2.instructor === auth_id) {
            new_releases.push(chck);
          }
        }
      }
      releases = new_releases;
      console.log({ new_releases });
    }

    dispatch({
      type: GET_ALL_RELEASES,
      payload: releases,
    });
  } catch (error) {
    console.log({ err: error.response });
    console.log({ err: error.message });
    if (error.response) {
      error = error.response.data.msg;
    } else {
      error = "Something went wrong. Try Again";
    }
    dispatch({
      type: GET_ALL_RELEASES,
      payload: [],
    });
  }
};

export const deleteReleasesAction = (id) => async (dispatch) => {
  dispatch({
    type: GET_ALL_RELEASES_LOADING,
    payload: true,
  });
  try {
    let releases = await deleteReleaseApi(id);
    let delete_msg = await (await deleteReleaseApi(id)).data.msg;
    releases = await getAllReleaseApi();
    console.log({ releases: releases.data.data });
    console.log({ delete_msg });
    dispatch({
      type: DELETE_RELEASE,
      payload: releases.data.data,
      msg: delete_msg,
    });
  } catch (error) {
    console.log({ err: error.response });
    console.log({ err: error.message });
    if (error.response) {
      error = error.response.msg;
    } else {
      error = "Something went wrong. Try Again";
    }
    dispatch({
      type: CREATE_RELEASE_ERROR,
      payload: [],
    });
  }
};
