import React, { Component } from "react";
import Dialog from "@material-ui/core/Dialog";
import { connect } from "react-redux";
import "./Form.scss";
import {
  clearReleaseAction,
  createReleaseAction,
  editReleaseAction,
} from "./../../../store/actions/Release";
import { Button } from "@material-ui/core";
import Loader from "react-loader-spinner";
import { FailModal, SuccessModal } from "./../../../components/Snacks/Response";

class Release_Form extends Component {
  constructor(props) {
    super(props);
    if (props.edit === false)
      this.state = {
        name: "",
        store: "",
        description: "",
        countdown: "",
        dueDate: "",
        error: "",
      };
    else {
      this.state = {
        ...props.edit,
      };
    }
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onClearError = this.onClearError.bind(this);
  }
  onSubmit(e) {
    let { name, store, description, countdown, dueDate } = this.state;
    if (name && store && description && dueDate) {
      if (this.props.edit === false)
        this.props.createReleaseAction({
          name,
          store,
          description,
          countdown: countdown ? countdown : 0,
          dueDate,
        });
      else
        this.props.editReleaseAction(this.props.edit._id, {
          name,
          store,
          description,
          countdown,
          dueDate,
        });
    } else {
      this.setState({ error: "All field must be filled" });
    }
  }
  onClearError() {
    this.props.clearReleaseAction();
    this.setState({ error: "" });
    this.props.handleClose();
    this.props.getAllRelease();
  }
  onChange(e) {
    e.preventDefault();
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  render() {
    let {
      release_loading,
      release_error,
      release_success,
      releases,
      msg,
    } = this.props.release;

    return (
      <div className=" release-form bg-red" style={{ padding: "17px 19px" }}>
        {(release_error || this.state.error) && (
          <FailModal msg={this.state.error || msg} onStop={this.onClearError} />
        )}

        {release_success && (
          <SuccessModal msg={msg} onStop={this.onClearError} />
        )}

        <div
          className="header mb-3"
          style={{ fontWeight: "bold", opacity: "0.7" }}
        >
          {`${this.props.edit === false ? "Create A" : "Edit"} Release`}
        </div>

        <form>
          <div className="flex">
            <div className="relative w-full mb-3 ml-1 mr-1">
              <label
                className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Name
              </label>
              <input
                className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="Name"
                name="name"
                value={this.state.name}
                onChange={this.onChange}
              />
            </div>
            <div className="relative w-full mb-5 ml-1 mr-1">
              <label
                className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Store
              </label>
              <select
                onChange={this.onChange}
                name="store"
                defaultValue={this.state.store}
                className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="Store"
              >
                <option>Select Store</option>
                <option>NewEgg</option>
                <option>Amazon</option>
                <option>Addidas</option>
              </select>
            </div>
          </div>
          <div className="flex">
            <div className="relative w-full mb-3 ml-1 mr-1">
              <label
                className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Countdown
              </label>
              <input
                onChange={this.onChange}
                type="number"
                name="countdown"
                value={Number(this.state.countdown)}
                defaultValue={0}
                min={0}
                className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="Countdown"
              />
            </div>
            <div className="relative w-full mb-3 ml-1 mr-1">
              <label
                className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Due Date
              </label>
              <input
                value={this.state.dueDate}
                name="dueDate"
                onChange={this.onChange}
                type="datetime-local"
                className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
              />
            </div>
          </div>
          <div className="relativ w-full mb-5 mt-5 ml-1 mr-1 w-50">
            <label
              className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
              htmlFor="grid-password"
            >
              Description
            </label>
            <textarea
              value={this.state.description}
              name="description"
              onChange={this.onChange}
              style={{ resize: "none" }}
              className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
              placeholder="Description"
            />
          </div>
          {!release_loading ? (
            <Button
              variant="contained"
              size="small"
              className="btn-sm btn-primary"
              onClick={this.onSubmit}
            >
              Submit
            </Button>
          ) : (
            <Button
              variant="contained"
              size="small"
              className="btn-sm btn-primary"
            >
              <Loader
                type="Oval"
                color="white"
                height={21}
                width={21}
                secondaryColor={"white"}
              />
            </Button>
          )}
        </form>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  release: state.release,
});

let ReleaseForm = connect(mapStateToProps, {
  clearReleaseAction,
  createReleaseAction,
  editReleaseAction,
})(Release_Form);

export default function AlertDialog({
  open,
  handleClose,
  getAllRelease,
  edit,
}) {
  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <ReleaseForm
          handleClose={handleClose}
          edit={edit}
          getAllRelease={getAllRelease}
        />
      </Dialog>
    </div>
  );
}
