import React, { Component } from "react";
import Search from "./../../../components/Search/Search";
import TaskForm from "./Form";
import Loader from "./../../../components/Snacks/Loader";
import { connect } from "react-redux";
import { FailModal, SuccessModal } from "./../../../components/Snacks/Response";
import { withRouter } from "react-router-dom";

import {
  getAllTasksAction,
  editTaskAction,
  createTaskAction,
  clearTaskAction,
  deleteTasksAction,
} from "./../../../store/actions/Task";

import TableDropdown from "./../../../components/Dropdowns/TableDropdown.js";
import { percent } from "../../../utils/NumFormat";
import { DATE } from "../../../utils/Moment";

class Task extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      edit: false,
      search: "",
    };
    this.onInputChange = this.onInputChange.bind(this);
    this.onClearError = this.onClearError.bind(this);
  }
  onClearError() {
    this.props.clearTaskAction();
    this.props.getAllTasksAction(this.props.match.params.id);
    this.setState({ open: false });
  }

  onInputChange(e, value) {
    this.setState({ search: e.target.value });
  }

  componentDidMount() {
    this.props.getAllTasksAction(this.props.match.params.id);
  }
  render() {
    let { color, Type, tasks, deleteTasksAction } = this.props;
    let { msg, tasks_loading, task_error, task_success } = this.props.task;
    return (
      <>
        {task_error && <FailModal msg={msg} onStop={this.onClearError} />}

        {task_success && <SuccessModal msg={msg} onStop={this.onClearError} />}

        {tasks_loading && <Loader />}

        <TaskForm
          edit={this.state.edit}
          open={this.state.open}
          getAllTask={this.props.getAllTasksAction}
          Id={this.props.match.params.id}
          handleClose={this.onClearError}
        />
        <div
          className={
            "relative flex mt-5 flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded " +
            (color === "light" ? "bg-white" : "bg-lightBlue-900 text-white")
          }
        >
          <div className="rounded-t mb-0 px-4 py-3 border-0">
            <div className="flex flex-wrap items-center">
              <div className="relative w-full px-2 max-w-full flex-grow flex-1">
                <h3
                  className={
                    "font-semibold text-lg " +
                    (color === "light" ? "text-blueGray-700" : "text-white")
                  }
                >
                  All Tasks ({" "}
                  {
                    tasks
                      .filter((task) => task.product_name !== undefined)
                      .filter(
                        (task) =>
                          task.product_name
                            .toLowerCase()
                            .indexOf(this.state.search.toLowerCase()) !== -1
                      ).length
                  }{" "}
                  )
                </h3>

                <small style={{ opacity: "0.5" }}></small>
              </div>
              <div className="">
                <Search
                  NaNPlus={true}
                  label="task"
                  Open={() => {
                    console.log("open");
                    this.setState({
                      open: true,
                      edit: false,
                    });
                  }}
                  onInputChange={this.onInputChange}
                />
              </div>
            </div>
          </div>
          <div className="block w-full overflow-x-auto">
            {/* Projects table */}
            <table className="items-center w-full bg-transparent border-collapse">
              <thead>
                <tr>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  >
                    Product Name
                  </th>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  >
                    Email
                  </th>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  >
                    Password
                  </th>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  >
                    CCV
                  </th>

                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  >
                    Status
                  </th>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  ></th>
                </tr>
              </thead>
              <tbody>
                {tasks &&
                  Array.isArray(tasks) &&
                  tasks
                    .filter((task) => task.product_name !== undefined)
                    .filter(
                      (task) =>
                        task.product_name
                          .toLowerCase()
                          .indexOf(this.state.search.toLowerCase()) !== -1
                    )
                    .map((data) => (
                      <tr>
                        <td className="border-t-0 font-weight-bold px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                          <div
                            className="lead"
                            style={{ fontSize: "15px", fontWeight: "normal" }}
                          >
                            {" "}
                            {data.product_name}
                          </div>
                          <div
                            style={{ opacity: "0.6" }}
                            className="mt-1 small"
                          >
                            {" "}
                          </div>
                        </td>
                        <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                          {data.email}
                        </td>
                        <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                          {data.password}
                        </td>
                        <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                          {data.ccv}
                        </td>
                        <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                          {data.status === "pending" && (
                            <i className="fas fa-circle text-orange-500 mr-2"></i>
                          )}
                          {data.status === "failed" && (
                            <i className="fas fa-circle text-red-500 mr-2"></i>
                          )}
                          {data.status === "success" && (
                            <i className="fas fa-circle text-teal-500 mr-2"></i>
                          )}{" "}
                          {data.status}
                        </td>
                        <td className="border-t-0 px-4 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4 text-right">
                          <span
                            className="fa fa-pen px-3 pnt"
                            onClick={() => {
                              this.setState({
                                open: true,
                                edit: data,
                              });
                            }}
                          />
                          <span
                            className="fa fa-trash px-3 pnt"
                            onClick={() => {
                              deleteTasksAction(data._id);
                            }}
                          />
                        </td>
                      </tr>
                    ))}
              </tbody>
            </table>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  task: state.task,
  tasks: state.task.tasks,
  releases: state.release.releases,
});

export default connect(mapStateToProps, {
  getAllTasksAction,
  editTaskAction,
  deleteTasksAction,
  createTaskAction,
  clearTaskAction,
})(withRouter(Task));
