import {
  AUTH_LOADING,
  AUTH_ERROR,
  CLEAR_AUTH_ERROR,
  AUTHENTICATED,
  LOGOUT,
  PROFILE_CHANGED,
  PROFILE_ERROR,
} from "../types";
import { AnyAction } from "redux";

let initialState = {
  profile: {},
  isAuth: null,
  token: null,
  // auth_type: "",
  auth_loading: false,
  refreshToken: "",
  error_msg: "",
  success_msg: "",
};

export default (state = initialState, action) => {
  console.log({ data: "In red" });
  switch (action.type) {
    case AUTHENTICATED:
      localStorage.setItem("reaper-token", action.payload.token);
      return {
        ...state,
        profile: action.payload.profile,
        token: action.payload.token,
        auth_loading: false,
        error_msg: "",
        isAuth: true,
      };

    case PROFILE_CHANGED:
      return {
        ...state,
        profile: action.payload,
        auth_loading: false,
        success_msg: "success",
      };
    case PROFILE_ERROR:
      return {
        ...state,
        error_msg: action.payload,
        success_msg: "",
        auth_loading: false,
      };
    case AUTH_LOADING:
      console.log({ aa: action.payload });
      return {
        ...state,
        auth_loading: true,
        error_msg: "",
        success_msg: "",
      };

    case CLEAR_AUTH_ERROR:
      return {
        ...state,
        auth_loading: false,
        error_msg: "",
        success_msg: "",
      };

    case AUTH_ERROR:
      return {
        ...state,
        auth_loading: false,
        isAuth: false,
        error_msg: action.payload,
        // auth_type: null,
        token: null,
      };

    case LOGOUT:
      localStorage.removeItem("reaper-token");
      return {
        ...initialState,
      };
    default:
      return state;
  }
};
