import React, { Component } from "react";
import Search from "./../../../components/Search/Search";
import ReleaseForm from "./Form";
import Loader from "./../../../components/Snacks/Loader";
import { connect } from "react-redux";
import { FailModal, SuccessModal } from "./../../../components/Snacks/Response";
import { Link } from "react-router-dom";

import {
  getAllReleasesAction,
  editReleaseAction,
  createReleaseAction,
  clearReleaseAction,
  deleteReleasesAction,
} from "./../../../store/actions/Release";

import TableDropdown from "./../../../components/Dropdowns/TableDropdown.js";
import { percent } from "../../../utils/NumFormat";
import { DATE } from "../../../utils/Moment";

class Release extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      edit: false,
      search: "",
    };
    this.onInputChange = this.onInputChange.bind(this);
    this.onClearError = this.onClearError.bind(this);
  }
  onClearError() {
    this.props.clearReleaseAction();
  }

  onInputChange(e, value) {
    this.setState({ search: e.target.value });
  }

  componentDidMount() {
    this.props.getAllReleasesAction();
  }
  render() {
    let { color, Type, releases, deleteReleasesAction } = this.props;
    let {
      msg,
      releases_loading,
      release_error,
      release_success,
    } = this.props.release;
    return (
      <>
        {release_error && <FailModal msg={msg} onStop={this.onClearError} />}

        {release_success && (
          <SuccessModal msg={msg} onStop={this.onClearError} />
        )}

        {releases_loading && <Loader />}

        <ReleaseForm
          edit={this.state.edit}
          open={this.state.open}
          getAllRelease={this.props.getAllReleasesAction}
          handleClose={() => {
            this.setState({
              open: false,
            });
          }}
        />
        <div
          className={
            "relative flex mt-5 flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded " +
            (color === "light" ? "bg-white" : "bg-lightBlue-900 text-white")
          }
        >
          <div className="rounded-t mb-0 px-4 py-3 border-0">
            <div className="flex flex-wrap items-center">
              <div className="relative w-full px-4 max-w-full flex-grow flex-1">
                <h3
                  className={
                    "font-semibold text-lg " +
                    (color === "light" ? "text-blueGray-700" : "text-white")
                  }
                >
                  {"Releases "}({" "}
                  {
                    releases.filter(
                      (release) =>
                        release.name
                          .toLowerCase()
                          .indexOf(this.state.search.toLowerCase()) !== -1
                    ).length
                  }{" "}
                  )
                </h3>
              </div>
              <div className="">
                <Search
                  label="release"
                  Open={() => {
                    console.log("open");
                    this.setState({
                      open: true,
                      edit: false,
                    });
                  }}
                  onInputChange={this.onInputChange}
                />
              </div>
            </div>
          </div>
          <div className="block w-full overflow-x-auto">
            {/* Projects table */}
            <table className="items-center w-full bg-transparent border-collapse">
              <thead>
                <tr>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  >
                    Releases
                  </th>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  >
                    Coutdown
                  </th>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  >
                    Status
                  </th>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  >
                    Due Time
                  </th>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  >
                    Completion
                  </th>
                  <th
                    className={
                      "px-6 align-middle border border-solid py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left " +
                      (color === "light"
                        ? "bg-blueGray-50 text-blueGray-500 border-blueGray-100"
                        : "bg-lightBlue-800 text-lightBlue-300 border-lightBlue-700")
                    }
                  ></th>
                </tr>
              </thead>
              <tbody>
                {Array.isArray(releases) &&
                  releases
                    .filter(
                      (release) =>
                        release.name
                          .toLowerCase()
                          .indexOf(this.state.search.toLowerCase()) !== -1
                    )
                    .map((data) => (
                      <tr>
                        <td className="border-t-0 font-weight-bold px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                          <Link to={`/admin/release/${data._id}`}>
                            <div
                              className="lead"
                              style={{ fontSize: "15px", fontWeight: "normal" }}
                            >
                              {" "}
                              {data.name}
                            </div>
                            <div
                              style={{ opacity: "0.6" }}
                              className="mt-1 small"
                            >
                              {" "}
                              {data.store}
                            </div>
                          </Link>
                        </td>
                        <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                          {data.countdown}
                        </td>
                        <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                          {data.failed === undefined ? (
                            <i className="fas fa-circle text-orange-500 mr-2"></i>
                          ) : (
                            <i className="fas fa-circle text-teal-500 mr-2"></i>
                          )}{" "}
                          on
                          {data.failed === undefined
                            ? " pending"
                            : " completed"}
                        </td>
                        <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                          <div className="flex">
                            {<DATE date={data.dueDate} />}
                          </div>
                        </td>
                        <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">
                          <div className="flex items-center">
                            <span className="mr-2">
                              {data.failed !== undefined
                                ? percent(data.failed, data.success)
                                : "0%"}
                            </span>
                            <div className="relative w-full">
                              <div className="overflow-hidden h-2 text-xs flex rounded bg-teal-200">
                                <div
                                  style={{
                                    width: `${percent(
                                      data.failed,
                                      data.success
                                    )}`,
                                  }}
                                  className={`shadow-none flex flex-col text-center whitespace-nowrap text-white justify-center ${
                                    data.failed === undefined
                                      ? "bg-orange-500"
                                      : "bg-teal-500"
                                  }`}
                                ></div>
                              </div>
                            </div>
                          </div>
                        </td>
                        <td className="border-t-0 px-4 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4 text-right">
                          <span
                            className="fa fa-pen px-3 pnt"
                            onClick={() => {
                              this.setState({
                                open: true,
                                edit: data,
                              });
                            }}
                          />
                          <span
                            className="fa fa-trash px-3 pnt"
                            onClick={() => {
                              deleteReleasesAction(data._id);
                            }}
                          />
                        </td>
                      </tr>
                    ))}
              </tbody>
            </table>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  release: state.release,
  releases: state.release.releases,
});

export default connect(mapStateToProps, {
  getAllReleasesAction,
  editReleaseAction,
  deleteReleasesAction,
  createReleaseAction,
  clearReleaseAction,
})(Release);
