import React from "react";

// components

import CardStats from "./../Cards/CardStats";

export default function HeaderStats({
  releasesNo,
  completeReleasesNo,
  uncompleteReleasesNo,

  tasksNo,
  successTasksNo,
  failedTasksNo,
  pendingTasksNo,

  pendingTasksNoPecent,
  successTasksNoPecent,
  failedTasksNoPecent,
}) {
  console.log({
    releasesNo,
    completeReleasesNo,
    uncompleteReleasesNo,

    tasksNo,
    successTasksNo,
    failedTasksNo,
    pendingTasksNo,

    pendingTasksNoPecent,
    successTasksNoPecent,
    failedTasksNoPecent,
  });
  return (
    <>
      {/* Header */}
      <div className="relative bg-white md:pt-32 pb-32 pt-12">
        <div className="px-4 md:px-10 mx-auto w-full">
          <div>
            {/* Card stats */}
            <div className="flex flex-wrap">
              <div className="w-full lg:w-6/12 xl:w-4/12 px-4">
                <CardStats
                  statSubtitle="Release"
                  main={releasesNo}
                  statArrow="right"
                  firstT={"completed"}
                  first={completeReleasesNo}
                  statPercentColor="text-emerald-500"
                  secondT={"pending"}
                  second={uncompleteReleasesNo}
                  statIconName="far fa-calendar-alt"
                  statIconColor="bg-red-500"
                />
              </div>
              <div className="w-full lg:w-6/12 xl:w-4/12 px-4">
                <CardStats
                  statSubtitle="Tasks"
                  main={tasksNo}
                  statArrow="down"
                  first={successTasksNo}
                  firstT={"successful"}
                  second={pendingTasksNo}
                  secondT={"pending"}
                  third={failedTasksNo}
                  thirdT={"failed"}
                  statIconName="fas fa-clock"
                  statIconColor="bg-orange-500"
                />
              </div>
              {/* <div className="w-full lg:w-6/12 xl:w-4/12 px-4">
                <CardStats
                  statSubtitle="Users"
                  statTitle="923234"
                  statArrow="down"
                  statPercent=""
                  statIconName="fas fa-users"
                  statIconColor="bg-pink-500"
                />
              </div> */}
              <div className="w-full lg:w-6/12 xl:w-4/12 px-4">
                <CardStats
                  statSubtitle="Performance"
                  main={"100%"}
                  statArrow="up"
                  first={successTasksNoPecent}
                  firstT={"successful"}
                  second={pendingTasksNoPecent}
                  secondT={"pending"}
                  third={failedTasksNoPecent}
                  thirdT={"failed"}
                  statIconName="fas fa-percent"
                  statIconColor="bg-lightBlue-500"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
