import {
  CREATE_TASK,
  GET_ALL_TASKS_LOADING,
  GET_ALL_TASKS,
  CREATE_TASK_LOADING,
  CREATE_TASK_ERROR,
  GET_TASK,
  CLEAR_TASK_POPUP,
  GET_RELEASE_TASKS,
  DELETE_TASK,
  // EDIT_TASK,
  // TASK_ERROR,
  // DELETE_TASK_FAIL,
  // DELETE_TASK_LOADING,
  // DELETE_TASK_SUCCESS,
} from "./../types";

const initialState = {
  task_error: true,
  task_success: null,
  release_tasks: [],
  task: {},
  msg: "",
  tasks: [],
  contents: [],
  tasks_loading: false,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case GET_ALL_TASKS:
      return {
        ...state,
        tasks: action.payload.reverse(),
        tasks_loading: false,
        msg: action.payload.msg,
      };
    case GET_RELEASE_TASKS:
      return {
        ...state,
        release_tasks: action.payload.reverse(),
        tasks_loading: false,
        msg: action.payload.msg,
      };

    case DELETE_TASK:
      return {
        ...state,
        tasks: action.payload,
        tasks_loading: false,
        task_success: true,
        msg: action.msg,
      };

    case GET_ALL_TASKS_LOADING:
      return {
        ...state,
        tasks_loading: true,
        task_success: false,
        task_error: false,
      };
    case CLEAR_TASK_POPUP:
      return {
        ...state,
        task_error: false,
        task_success: false,
        tasks_loading: false,
        msg: "",
      };
    case CREATE_TASK_LOADING:
      return {
        ...state,
        task_loading: true,
        task_error: null,
        task_success: false,
      };
    case CREATE_TASK_ERROR:
      return {
        ...state,
        task_loading: false,
        task_error: true,
        task_success: false,
        msg: action.payload,
      };
    case CREATE_TASK:
      return {
        ...state,
        task_loading: false,
        task_success: true,
        task_error: null,
        task: action.payload,
        msg: action.msg,
      };
    case GET_TASK:
      return {
        ...state,
        task_loading: false,
        task_success: false,
        task_error: null,
        task: action.payload,
      };
    default:
      // return state;
      return { ...state };
  }
};
