import React from "react";
import Button from "@material-ui/core/Button";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import { makeStyles } from "@material-ui/core/styles";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    "& > * + *": {
      marginTop: theme.spacing(2)
    }
  }
}));

export function SuccessModal({ onStop, msg }) {
  console.log({ msg });
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Snackbar open={true} autoHideDuration={2000} onClose={onStop}>
        <Alert onClose={onStop} severity="success">
          {msg}
        </Alert>
      </Snackbar>
    </div>
  );
}
export function FailModal({ onStop, msg }) {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Snackbar open={true} autoHideDuration={2000} onClose={onStop}>
        <Alert onClose={onStop} severity="error">
          {msg}
        </Alert>
      </Snackbar>
    </div>
  );
}
