import React, { Component } from "react";
import Dialog from "@material-ui/core/Dialog";
import { connect } from "react-redux";
import "./form.scss";
import {
  clearTaskAction,
  createTaskAction,
  editTaskAction,
  getReleaseTasksAction,
} from "./../../../store/actions/Task";
import { Button } from "@material-ui/core";
import Loader from "react-loader-spinner";
import { FailModal, SuccessModal } from "./../../../components/Snacks/Response";

class Task_Form extends Component {
  constructor(props) {
    super(props);
    if (props.edit === false)
      this.state = {
        product_name: "",
        email: "",
        password: "",
        ccv: "",
        item_url: "",
        error: "",
      };
    else {
      this.state = {
        ...props.edit,
      };
    }
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onClearError = this.onClearError.bind(this);
  }
  onSubmit(e) {
    let { product_name, email, password, ccv, item_url } = this.state;
    if (product_name && email && password && ccv && item_url) {
      if (this.props.edit === false)
        this.props.createTaskAction(this.props.Id, {
          product_name,
          email,
          password,
          ccv,
          item_url,
        });
      else
        this.props.editTaskAction(this.props.edit._id, {
          product_name,
          email,
          password,
          ccv,
          item_url,
        });
    } else {
      this.setState({ error: "All field must be filled" });
    }
  }
  onClearError() {
    this.props.clearTaskAction();
    this.setState({ error: "" });
    this.props.handleClose();
    this.props.getReleaseTasksAction(this.props.Id);
  }
  onChange(e) {
    e.preventDefault();
    this.setState({
      [e.target.name]: e.target.value,
    });
  }
  render() {
    let {
      release_loading,
      release_error,
      release_success,
      releases,
      msg,
    } = this.props.release;

    return (
      <div className=" release-form bg-red" style={{ padding: "17px 19px" }}>
        {(release_error || this.state.error) && (
          <FailModal
            msg={this.state.error || msg}
            onStop={this.props.handleClose}
          />
        )}

        {release_success && (
          <SuccessModal msg={msg} onStop={this.props.handleClose} />
        )}

        <div
          className="header mb-3"
          style={{ fontWeight: "bold", opacity: "0.7" }}
        >
          {`${this.props.edit === false ? "Create A" : "Edit"} Task`}
        </div>

        <form>
          <div className="flex">
            <div
              style={{ width: "50%", maxWidth: "340px" }}
              className="relative w-full mb-3 ml-1 mr-1"
            >
              <label
                className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Product Name
              </label>
              <input
                className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="Product Name"
                name="product_name"
                value={this.state.product_name}
                onChange={this.onChange}
              />
            </div>
            <div className="relative w-full mb-5 ml-1 mr-1">
              <label
                className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Email
              </label>
              <input
                onChange={this.onChange}
                name="email"
                defaultValue={this.state.email}
                className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="Email"
              />
            </div>
          </div>
          <div className="flex">
            <div className="relative w-full mb-3 ml-1 mr-1">
              <label
                className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Password
              </label>
              <input
                onChange={this.onChange}
                placeholder="Password"
                name="password"
                value={this.state.password}
                className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
                placeholder="Password"
              />
            </div>
            <div className="relative w-full mb-3 ml-1 mr-1">
              <label
                className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                CCV
              </label>
              <input
                value={this.state.ccv}
                name="ccv"
                onChange={this.onChange}
                type="number"
                maxLength={3}
                placeholder="CCV"
                className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
              />
            </div>
          </div>
          <div className="relativ w-full mb-5 mt-5 ml-1 mr-1 w-50">
            <label
              className="block uppercase text-blueGray-600 text-xs font-bold mb-2"
              htmlFor="grid-password"
            >
              Product Link
            </label>
            <textarea
              value={this.state.item_url}
              name="item_url"
              onChange={this.onChange}
              style={{ resize: "none" }}
              className="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150"
              placeholder="Product Link"
            />
          </div>
          {!release_loading ? (
            <Button
              variant="contained"
              size="small"
              className="btn-sm btn-primary"
              onClick={this.onSubmit}
            >
              Submit
            </Button>
          ) : (
            <Button
              variant="contained"
              size="small"
              className="btn-sm btn-primary"
            >
              <Loader
                type="Oval"
                color="white"
                height={21}
                width={21}
                secondaryColor={"white"}
              />
            </Button>
          )}
        </form>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  release: state.release,
  creator: state.auth.profile._id,
});

let TaskForm = connect(mapStateToProps, {
  clearTaskAction,
  createTaskAction,
  editTaskAction,
  getReleaseTasksAction,
})(Task_Form);

export default function AlertDialog({
  open,
  handleClose,
  getAllTask,
  edit,
  Id,
}) {
  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <TaskForm
          handleClose={handleClose}
          edit={edit}
          getAllTask={getAllTask}
          Id={Id}
        />
      </Dialog>
    </div>
  );
}
