import {
  RegisterApi,
  LoginApi,
  editProfile,
  editProfileInstructor,
} from "./../api/service";
import { Dispatch } from "redux";
import {
  AUTHENTICATED,
  AUTH_LOADING,
  LOGOUT,
  AUTH_ERROR,
  PROFILE_ERROR,
  CLEAR_AUTH_ERROR,
  PROFILE_CHANGED,
} from "../types";

export const clearAuthErrorAction = () => async (dispatch) => {
  dispatch({ type: CLEAR_AUTH_ERROR });
};

export const RegisterAction = (regDetails, history) => async (dispatch) => {
  dispatch({
    type: AUTH_LOADING,
    payload: true,
  });
  try {
    let register = await RegisterApi(regDetails);
    console.log({ register: register.data });
    dispatch({
      type: AUTHENTICATED,
      payload: register.data,
    });
    await history.push("/admin/dashboard");
  } catch (error) {
    console.log({ err: error.response });
    if (error.response) {
      error = error.response.data.msg;
    } else {
      error = "Something went wrong. Try Again";
    }
    dispatch({
      type: AUTH_ERROR,
      payload: error,
    });
  }
};

export const LoginAction = (loginDetails, history) => async (dispatch) => {
  console.log({ loginDetails });

  dispatch({
    type: AUTH_LOADING,
    payload: true,
  });
  try {
    let login = await LoginApi(loginDetails);
    console.log({ login: login.data });
    dispatch({
      type: AUTHENTICATED,
      payload: login.data,
    });
    await history.push("/admin/dashboard");
  } catch (error) {
    console.log(error.response);
    console.log(error.message);
    if (error.response) {
      error = error.response.data.msg;
    } else {
      error = "Something went wrong. Try Again";
    }
    console.log({ error });
    dispatch({
      type: AUTH_ERROR,
      payload: error,
    });
  }
};

export const ProfileUpdate = (profileData) => async (dispatch, state) => {
  console.log({ profileData });
  try {
    await dispatch({ type: AUTH_LOADING });
    let Profile = await state().auth;
    let user;
    user = await editProfile(profileData);

    user = await user.data.data;
    // user = { ...Profile, user };

    console.log({ user });
    await dispatch({
      type: PROFILE_CHANGED,
      payload: user,
    });
  } catch (error) {
    console.log(error.response);
    console.log(error.message);
    if (error.response) {
      error = error.response.data.message
        ? error.response.data.message[0]
        : error.response.data.data;
    } else {
      error = "Something went wrong. Try Again";
    }
    console.log({ error });
    dispatch({
      type: PROFILE_ERROR,
      payload: error,
    });
  }
};

export const LogoutAction = (history) => async (dispatch) => {
  console.log("logout");
  dispatch({ type: LOGOUT });
  history.push("/login");
};
