import React, { Component } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import { LogoutAction } from "./../store/actions/Auth";
import { connect } from "react-redux";
import { withRouter } from "react-router";
// components

import AdminNavbar from "./../components/Navbars/AdminNavbar.js";
// import FooterAdmin from "components/Footers/FooterAdmin.js";
// import Tables from "views/admin/Tables";

import Sidebar from "./../components/Sidebar/Sidebar.js";
import HeaderStats from "./../components/Headers/HeaderStats.js";

import SingleRelease from "./../views/admin/singleRelease/index";
import Tasks from "./../views/admin/Task/index";
import Release from "./../views/admin/Release/index";

import Dashboard from "./../views/admin/Dashboard";

class Admin extends Component {
  render() {
    let { LogoutAction } = this.props;
    return (
      <>
        <Sidebar
          logOut={() => {
            LogoutAction(this.props.history);
          }}
        />
        <div className="relative md:ml-64 bg-white">
          <Switch>
            <Route path="/admin/dashboard" exact component={Dashboard} />
            <Route path="/admin/release" exact component={Release} />
            <Route path="/admin/release/:id" exact component={SingleRelease} />
            <Route path="/admin/tasks" exact component={Tasks} />

            <Redirect from="/admin" to="/admin/dashboard" />
          </Switch>
          {/* <div className="px-4 md:px-10 mx-auto w-full -m-24">
          <FooterAdmin />
        </div> */}
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => ({});
export default connect(mapStateToProps, { LogoutAction })(withRouter(Admin));
