import { API } from "./base";

export const RegisterApi = async (userDetails) => {
  let AUTH_API = await API();
  return AUTH_API.post(`/user/register`, userDetails);
};
export const LoginApi = async (loginDetails) => {
  let AUTH_API = await API();
  return AUTH_API.post(`/user/login`, loginDetails);
};

export const editProfile = async (profileDetails) => {
  let AUTH_API = await API();
  return AUTH_API.patch(`/account/updateUser`, profileDetails);
};





export const getAllReleaseApi = async (releaseData) => {
  let AUTH_API = await API();
  return AUTH_API.get(`/allRelease`, releaseData);
};

export const createReleaseApi = async (releaseData) => {
  let AUTH_API = await API();
  return AUTH_API.post(`/release`, releaseData);
};

export const updateReleaseApi = async (id, releaseData) => {
  let AUTH_API = await API();
  return AUTH_API.patch(`/release/${id}`, releaseData);
};

export const deleteReleaseApi = async (id) => {
  let AUTH_API = await API();
  return AUTH_API.delete(`/release/${id}`);
};

export const getAllTaskApi = async (taskData) => {
  let AUTH_API = await API();
  return AUTH_API.get(`/tasks`, taskData);
};




export const getReleaseTaskApi = async (id, taskData) => {
  let AUTH_API = await API();
  return AUTH_API.get(`/task/${id}`, taskData);
};

export const createTaskApi = async (id, taskData) => {
  let AUTH_API = await API();
  return AUTH_API.post(`/task/${id}`, taskData);
};

export const updateTaskApi = async (id, taskData) => {
  let AUTH_API = await API();
  return AUTH_API.patch(`/task/${id}`, taskData);
};

export const deleteTaskApi = async (id) => {
  let AUTH_API = await API();
  return AUTH_API.delete(`/task/${id}`);
};
