import React from "react";
import Moment from "react-moment";

export class DATE extends React.Component {
  render() {
    let { date } = this.props;
    return (
      <Moment format="dddd, MMMM Do YYYY, h:mm a" withTitle>
        {date}
      </Moment>
    );
  }
}

export function SumUp(a) {
  let all = 0;
  let num;
  if (a) {
    for (num of a) {
      all = all + num.totalStudentAttendingCourse;
    }
  } else {
    for (num of []) {
      all = all + num.totalStudentAttendingCourse;
    }
  }
  return all;
}
